import { Inject, Injectable, NgZone } from '@angular/core';
import { NotificationsHttpService } from '@backbase/data-ang/notifications';
import { NotificationStreamItem, shareReplayOnce } from '@backbase/notifications-common-ang';
import { delay, delayWhen, first, map, observeOn, switchMap, take, tap } from 'rxjs/operators';
import { asyncScheduler, combineLatest, merge, Observable, of, Subject } from 'rxjs';
import {
  enterZone,
  leaveZone,
  NOTIFICATIONS_BADGE_FORCE_UPDATE_EVENT,
  NOTIFICATIONS_CREATE_LOCAL_EVENT,
  BadgeForceUpdateEventPayload,
} from '@backbase/notifications-common-ang';

import {
  fromHttpResponse,
  notificationsStreamToPopups,
  notificationsStreamToSticky,
  notificationLocalToStreamItem,
} from '../helpers/mappers';
import { PUBSUB, Pubsub } from '@backbase/foundation-ang/web-sdk';
import { NotificationLocalItem } from '../models/notifications-local-item.model';
import { NOTIFICATION_LOCAL } from '../constants/notification-local';
import { WidgetPropertiesService } from './widget-properties.service';

export const DEFAULT_POLLING_INTERVAL = 15;

@Injectable()
export class NotificationsPopupsDataService {
  /**
   * Sticky notifications stream
   */
  readonly stickyNotificationsStream$: Observable<NotificationStreamItem[]>;

  /**
   * Popup notifications stream
   */
  readonly popupsNotificationsStream$: Observable<NotificationStreamItem[]>;

  /**
   * Mark notification as read stream
   */
  readonly markNotificationAsRead$ = new Subject<NotificationStreamItem | string>();

  private readonly notificationsStream$: Observable<NotificationStreamItem[]>;
  private readonly notificationsStreamForceUpdate$ = new Subject<NotificationStreamItem[]>();
  private readonly polling$ = new Subject<void>();
  private readonly notificationsLocalStickySource = new Map<string, NotificationStreamItem>();

  private readonly origins$ = this.properties.origins$;
  private readonly severityLevels$ = this.properties.severityLevels$;
  private readonly pollingInterval$ = this.properties.pollingInterval$;

  constructor(
    private readonly dataHttpService: NotificationsHttpService,
    private readonly ngZone: NgZone,
    private readonly properties: WidgetPropertiesService,
    @Inject(PUBSUB) private readonly pubSub: Pubsub,
  ) {
    this.notificationsStream$ = this.initializeNotificationsStreamPolling();
    this.stickyNotificationsStream$ = this.notificationsStream$.pipe(map(notificationsStreamToSticky));
    this.popupsNotificationsStream$ = this.notificationsStream$.pipe(map(notificationsStreamToPopups));

    this.markNotificationAsRead$.subscribe((payload: NotificationStreamItem | string) => {
      this.pubSub.publish<undefined | BadgeForceUpdateEventPayload>(
        NOTIFICATIONS_BADGE_FORCE_UPDATE_EVENT,
        typeof payload === 'object'
          ? {
              origin: payload.origin,
              level: payload.level,
            }
          : undefined,
      );
    });

    this.pubSub.subscribe(NOTIFICATIONS_CREATE_LOCAL_EVENT, (notification: NotificationLocalItem) => {
      const parsedNotification = notificationLocalToStreamItem(notification);

      if (notification.sticky) {
        this.notificationsLocalStickySource.set(parsedNotification.id, parsedNotification);
      }

      this.notificationsStream$
        .pipe(
          switchMap(stream => {
            const popup = (!notification.sticky && [parsedNotification]) || [];
            const stickies = this.notificationsLocalSticky;

            return of([...stream, ...stickies, ...popup]);
          }),
          map(concatenatedStream => Array.from(new Map(concatenatedStream.map(item => [item.id, item])).values())),
          take(1),
        )
        .subscribe(notificationsStream => this.notificationsStreamForceUpdate$.next(notificationsStream));
    });
  }

  private get notificationsLocalSticky(): NotificationStreamItem[] {
    return Array.from(this.notificationsLocalStickySource.values());
  }

  /**
   * Mark notification as read
   *
   * @param notification Notification data
   */
  markNotificationAsRead(notification: NotificationStreamItem | string): void {
    const id = typeof notification === 'string' ? notification : notification.id;

    if (id.match(NOTIFICATION_LOCAL)) {
      this.removeNotificationItemFromStream(id);
      this.notificationsLocalStickySource.delete(id);
    } else {
      this.dataHttpService.putMarkAsRead({ id, markAsReadPutRequestBody: { read: true } }).subscribe(() => {
        this.removeNotificationItemFromStream(id);
        this.markNotificationAsRead$.next(notification);
      });
    }
  }

  private requestNotificationsStream(): Observable<NotificationStreamItem[]> {
    return combineLatest([this.pollingInterval$, this.origins$, this.severityLevels$]).pipe(
      first(),
      switchMap(([interval, origins, levels]) =>
        this.dataHttpService.getNotificationsStream(
          {
            interval,
            ...(origins.length && { origins }),
            ...(levels && { levels: levels.join(', ') }),
          },
          'response',
        ),
      ),
      map(fromHttpResponse),
    );
  }

  private removeNotificationItemFromStream(notificationId: string) {
    this.notificationsStream$
      .pipe(
        first(),
        map(notificationsStream => notificationsStream.filter(({ id }) => id !== notificationId)),
      )
      .subscribe(notificationsStream => this.notificationsStreamForceUpdate$.next(notificationsStream));
  }

  private initializeNotificationsStreamPolling(): Observable<NotificationStreamItem[]> {
    const pollAndRequest$ = of(undefined).pipe(
      tap(() => this.polling$.next()),
      switchMap(() => this.requestNotificationsStream()),
    );
    const pollingNotificationsStream$ = this.polling$.pipe(
      switchMap(() => this.pollingInterval$),
      delayWhen(pollingInterval => of().pipe(delay(pollingInterval, leaveZone(this.ngZone, asyncScheduler)))),
      switchMap(() => pollAndRequest$),
      observeOn(enterZone(this.ngZone, asyncScheduler)),
    );

    return merge(
      this.notificationsStreamForceUpdate$,
      pollingNotificationsStream$.pipe(switchMap(stream => of([...stream, ...this.notificationsLocalSticky]))),
      pollAndRequest$.pipe(switchMap(stream => of([...stream, ...this.notificationsLocalSticky]))),
    ).pipe(shareReplayOnce());
  }
}
