import { Injectable, Optional } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';
import {
  getPropertyValue,
  modelItemToBoolean,
  secondsToMilliseconds,
  BaseWidgetPropertiesService,
  shareReplayOnce,
} from '@backbase/notifications-common-ang';
import { ItemModel } from '@backbase/foundation-ang/core';

export interface WidgetProperties {
  allowedRoutes: string[];
  autofocus: boolean;
  popupMessageLineAmount: number;
  displayShowMoreButtonInPopup: boolean;
  origins: string[];
  severityLevels: string[];
  pollingInterval: number;
}

export const DefaultWidgetProperties: WidgetProperties = {
  allowedRoutes: [],
  autofocus: true,
  popupMessageLineAmount: 2,
  displayShowMoreButtonInPopup: false,
  origins: [],
  severityLevels: [],
  pollingInterval: 15,
};

@Injectable()
export class WidgetPropertiesService extends BaseWidgetPropertiesService {
  constructor(@Optional() protected readonly itemModel?: ItemModel) {
    super(itemModel);
  }

  private readonly autofocus$$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly popupMessageLineAmount$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly displayShowMoreButtonInPopup$$ = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly pollingInterval$$ = new BehaviorSubject<number | undefined>(undefined);

  readonly autofocus$ = getPropertyValue(this.autofocus$$, this.autofocus);
  readonly popupMessageLineAmount$ = getPropertyValue(this.popupMessageLineAmount$$, this.popupMessageLineAmount);
  readonly displayShowMoreButtonInPopup$ = getPropertyValue(
    this.displayShowMoreButtonInPopup$$,
    this.displayShowMoreButtonInPopup,
  );
  readonly pollingInterval$ = getPropertyValue(this.pollingInterval$$, this.pollingInterval);

  private get autofocus(): Observable<boolean> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.autofocus);
    }

    return this.itemModel
      .property('autofocus', DefaultWidgetProperties.autofocus)
      .pipe(map(modelItemToBoolean), first());
  }

  private get popupMessageLineAmount(): Observable<number> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.popupMessageLineAmount);
    }

    return this.itemModel.property('popupMessageLineAmount', '').pipe(map(Number), first());
  }

  private get displayShowMoreButtonInPopup(): Observable<boolean> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.displayShowMoreButtonInPopup);
    }

    return this.itemModel
      .property('displayShowMoreButtonInPopup', DefaultWidgetProperties.displayShowMoreButtonInPopup)
      .pipe(map(modelItemToBoolean), first());
  }

  private get pollingInterval(): Observable<number> {
    if (!this.itemModel) {
      return this.defaultModelObservable(DefaultWidgetProperties.pollingInterval * 1000);
    }

    return this.itemModel.property('pollingInterval', DefaultWidgetProperties.pollingInterval).pipe(
      map(Number),
      map(n => n * 1000),
      shareReplayOnce(),
    );
  }

  setAutofocus(value: string | boolean): void {
    this.autofocus$$.next(modelItemToBoolean(value));
  }

  setPopupMessageLineAmount(value: string | number): void {
    this.popupMessageLineAmount$$.next(Number(value));
  }

  setDisplayShowMoreButtonInPopup(value: string | boolean): void {
    this.displayShowMoreButtonInPopup$$.next(modelItemToBoolean(value));
  }

  setPollingInterval(value: string | number): void {
    const pollingInterval = Number(value);
    if (!isNaN(pollingInterval) && pollingInterval > 0) {
      this.pollingInterval$$.next(secondsToMilliseconds(pollingInterval));
    }
  }
}
