import { HttpResponse } from '@angular/common/http';
import { NotificationStreamItem } from '@backbase/notifications-common-ang';
import { SeverityLevel } from '@backbase/data-ang/notifications';
import { NotificationSeverityType } from '../models/notification-severity-type.model';
import { NotificationLocalItem } from '../models/notifications-local-item.model';
import { NOTIFICATION_LOCAL } from '../constants/notification-local';

export const secondsToMilliseconds = (seconds: number) => seconds * 1000;

export const fromHttpResponse = <T>(response: HttpResponse<T>) => {
  if (response.body === null) {
    throw new Error('Response body not set');
  }

  return response.body;
};

export const notificationsStreamToSticky = (notificationsStream: NotificationStreamItem[]): NotificationStreamItem[] =>
  notificationsStream.filter(item => !!item.expiresOn);
export const notificationsStreamToPopups = (notificationsStream: NotificationStreamItem[]): NotificationStreamItem[] =>
  notificationsStream.filter(item => !item.expiresOn);

// eslint-disable-next-line arrow-body-style
export const parseNotificationSeverityLevel = (level: SeverityLevel): NotificationSeverityType => {
  return level === 'ALERT' ? 'error' : (level.toLowerCase() as NotificationSeverityType);
};

// eslint-disable-next-line arrow-body-style
export const severityTypeToSeverityLevel = (type: NotificationSeverityType): SeverityLevel => {
  return type === 'error' ? 'ALERT' : (type.toUpperCase() as SeverityLevel);
};

export const notificationLocalToStreamItem = (notification: NotificationLocalItem): NotificationStreamItem => {
  const timestamp = Date.now();
  const date = new Date(timestamp).toISOString();

  return {
    id: `${NOTIFICATION_LOCAL}_${timestamp + Math.floor(Math.random() * 1000)}`,
    createdOn: date,
    title: notification.title,
    message: notification.message,
    level: severityTypeToSeverityLevel(notification.level),
    ...(notification.sticky && { expiresOn: date }),
  };
};
